	module.exports = {"data":{"post":{"html":"<p>After saying that <a href=\"http://www.papascott.de/archives/2004/08/10/phish-me-not/\">that I hadn't heard of any phishing</a> for German online banking passwords, I've now come across three in the past two weeks. <a href=\"http://vowe.net/archives/004845.html\" title=\"vowe dot net :: Isch 'abe gar keine Postbank Konto\">The latest</a> involves the Postbank, and gets around PIN/TAN authentication by asking for a TAN directly on the fake login page. I assume the \"login\" is never successful to make you keep entering new TANs. They copied the layout pretty exactly, even the commented CVS information is in the source. If you're curious look at postbanks dot info.</p>","fields":{"title":"I take it all back","date":"20 Aug 2004","url":"https://papascott.de/2004-08-20-i-take-it-all-back/"}}},"pathContext":{"slug":"/2004-08-20-i-take-it-all-back/","prev":{"fields":{"title":"Four Weeks for a Mug","date":"20 Aug 2004","slug":"/2004-08-20-four-weeks-for-a-mug/"}},"next":{"fields":{"title":"Olympic Schadenfreude","date":"21 Aug 2004","slug":"/2004-08-21-olympic-coverage/"}}}}

